import React, {Component} from 'react'
import PropTypes from 'prop-types';

import {Card, Tabs, Button, Form, Select, Input} from 'antd'
import './index.scss'

class LoginUser extends Component {
  static contextTypes = {
    router: PropTypes.object,
  }


  constructor(props, context) {
    super(props, context);

    this.state = {
      schoolList: ['1', '2', '3']
    }

    // this.handleChange = this.handleChange.bind(this)
    // this.handleSubmit = this.handleSubmit.bind(this)
  }

  handleChange = (val) => {
    const schoolList = this.state.schoolList

    // fetch schoolList
    this.setState({
      schoolList: schoolList.filter(_ => _.includes(val))
    })
  }
  handleForgetPassword = () => {
  }

  handleSubmit = () => {
    this.context.router.push('/userInfo')
  }

  render() {
    return (
      <Card className="loginUser">
        <div class="align-center">
          <img src={require('@/assets/student-logo.png')}/>
        </div>
        <Tabs className="loginUser-tab" defaultActiveKey="1">
          <Tabs.TabPane tab="" key="1">
            <Form>
              <Form.Item>
                <Select placeholder="" onChange={this.handleChange} combobox>
                  {this.state.schoolList.map(_ => <Select.Option key={_}>{_}</Select.Option>)}
                </Select>
              </Form.Item>
              <Form.Item>
                <Input size="large" placeholder=""/>
              </Form.Item>
              <Form.Item>
                <Input type="password" size="large" placeholder=""/>
              </Form.Item>
              <Button className="w-12" size="large" type="primary"></Button>
              <span class="f-12 mt-10" onClick={this.handleForgetPassword}></span>
            </Form>
          </Tabs.TabPane>
          <Tabs.TabPane tab="" key="2">
            <Form>
              <Form.Item>
                <Input size="large" placeholder="//"/>
              </Form.Item>
              <Form.Item>
                <Input type="password" size="large" placeholder=""/>
              </Form.Item>
            </Form>
            <Button className="w-12" size="large" type="primary" onClick={this.handleSubmit}></Button>
          </Tabs.TabPane>
        </Tabs>
      </Card>
    )
  }
}

export default LoginUser



